.contact-section {
  height: 1000px;
  position: relative;
}

#dark .contact-section {
  color: white;
  background-color: #38004c;
}

.image-class img {
  height: 650px;
  width: 100%;
  border-radius: 20px;
  object-fit: cover;
}

.contact-form-design {
  padding: 30px;
  width: 100%;
  box-shadow: 15px 9px 9px -2px rgba(230, 224, 224, 0.86);
  -webkit-box-shadow: 15px 9px 9px -2px rgba(230, 224, 224, 0.86);
  -moz-box-shadow: 15px 9px 9px -2px rgba(230, 224, 224, 0.86);
}

.contact-form input {
  height: 50px;
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  width: 100%;
}

#dark .contact-form input {
  height: 50px;
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  color: white;

  width: 100%;
  background-color: #38004c;
}

#dark .contact-form-design {
  background-color: #38004c;

  box-shadow: -6px 11px 5px -4px rgba(0, 0, 0, 0.42);
  -webkit-box-shadow: -6px 11px 5px -4px rgba(0, 0, 0, 0.42);
  -moz-box-shadow: -6px 11px 5px -4px rgba(0, 0, 0, 0.42);
}

.contact-form textarea {
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  width: 100%;
}

#dark .contact-form textarea {
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  width: 100%;
  background-color: #38004c;
  color: white;

}

.custom-select {
  height: 50px;
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  width: 100%;
}

#dark .custom-select {
  height: 50px;
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  width: 100%;
  color: white;
  background-color: #38004c;
}

.button-submit p {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  border-radius: 30px;
  background-color: rgb(32, 14, 5);
  color: greenyellow;
  padding: 10px;
  width: 50%;
  margin-top: 40px;
  cursor: pointer;
}

.button-submit p:hover {
  background-color: rgb(226, 7, 211);
  color: white;
  transition-duration: 2s;
}

@media (max-width: 1000px) {
  .image-class img {
    display: none;
  }
}
